<template>
    <div class = "container mt-10">
        <div class = " row ">
            <div class = "col-sm-6 mt-5 card mx-auto">
                <div class="card-body m-3">
                   <h4 class="text-left">{{ message}} </h4>
                </div>
                <form v-cloak>
                  <input type="text" placeholder = "Username"  v-model="username" v-bind:class="{ 'error-border': inputUsernameError }" class="form-control p-3 mt-3" /><br>
                  <div v-if="showUsernameError"> <span class="error">Please enter a correct username</span></div>
                  <input type="password" placeholder = "Password" v-model="password" v-bind:class="{ 'error-border': inputPasswordError }" class=" form-control p-3 mt-3" /><br>
                  <div  v-if="showPasswordError"><span class="error" >Please enter a correct password</span> </div><br>
                   <button type= " button" v-on:click="login" class=" btn btn-primary btn-lg btn-block m-3">Submit</button>
                </form> 
            </div>
         </div>
    </div>
</template>

<script>
export default {
  name: 'Login',
  props: ['message', 'showUsernameError', 'inputUsernameError','showPasswordError','inputPasswordError'],
 
  methods: {
    login: function(){
        const app = this;
        app.$emit('clicked', this.username, this.password)
    }
}
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" >

$main-bg-color: #55552b;
$hover-color: #00004d;
$error-color: #bf3000;

[v-cloak]{
    display: none;
    }
      .card{
          box-shadow: 4px 8px 8px 0px rgba(0,0,0,0.2);
          .card-body{
              h4{
                  font-size: 28px;
              }
          }
      }
       
     .error{
         color: $error-color;
     }  
     .error-border {
         border: 1px solid $error-color;
     }
     .btn-primary {
        background-color:$main-bg-color;
}
 .btn:hover{
            background-color:$hover-color;
     }
    
</style>